.Page,
.Page1,
.Page2 {
  z-index: 9999;
  top: 0px;
  min-height: 1200px;
  z-index: 9999;
  overflow-y: auto;
}

.Page h1,
.Page2 h1,
.Page3 h1 {
  font-size: 3em;
  margin-block-end: 0px;
}
.Page h3,
.Page2,
.Page3 {
  margin-block-start: 0px;
}
.Link {
  color: black;
}
.Link:hover {
  color: skyblue;
}

.wrapper {
  padding: 10px;
  width: 600px;
  height: 100%;
  background-color: white;
  background: rgb(
    204,
    204,
    204
  ); /* Fallback for older browsers without RGBA-support */
  background: rgba(204, 204, 204, 1);
  z-index: 1;
  margin: 0px auto;
}
.Column {
  display: flex;
  flex-direction: column;
}
.whiteBox {
  background-color: white;
  padding: 10px;
  width: 100%;
  margin: 15px auto;
  border-radius: 10px;
  border: 1px solid;
  cursor: pointer;
}
.whiteBox span {
  display: block;
  padding: 5px;
  width: 90%;
}

.Row {
  display: flex;
  flex-direction: row;
  align-items: center;

  justify-content: center;
}
.Column {
  display: flex;
  flex-direction: column;
}
.MarrigeElement {
  margin: 10px;
  padding: 10px;
}
.WhiteBox {
  background-color: white;
  width: 60%;
  margin: 0px auto 15px auto;
  background: rgb(
    204,
    204,
    204
  ); /* Fallback for older browsers without RGBA-support */
  background: rgba(204, 204, 204, 0.5);
}
.WhiteBox h1 span {
  position: absolute;
  left: 0px;
  width: 20px;
  transform-origin: bottom center;
}
.Splitter {
  height: 1px;
  background-color: black;
  width: 80%;
  margin: 0 auto;
}
.Text {
  width: 60%;
  display: block;
  margin: 0 auto;
  font-size: 12px;
  padding: 10px;
}

.Address {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  width: 80%;
  align-items: center;
  justify-content: center;
}
.Grid1 {
  border-right: 1px solid;
  padding: 10px;
  width: 33%;
}
.Grid2 {
  display: flex;
  flex-direction: column;
  border-right: 1px solid;
  width: 33%;
  padding: 10px;
}
.Grid3 {
  display: flex;
  flex-direction: column;
  padding: 10px;
  width: 33%;
}
.Grid3 span,
.Grid1 span,
.Grid2 span {
  line-height: 1.5em;
}
.spanMarriage {
  font-size: 1.5em;
}
.hearth {
  color: red;
  font-size: 1.8em;
}
.RowActions {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 50px;
  padding-bottom: 20px;
}
.RowActions div > a {
  padding: 10px;
  border: 1px solid;
  margin: 10px;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
}
.RowActions div > a:hover {
  background-color: white;
  color: black;
  text-decoration: underline;
}

@media only screen and (max-width: 1200px) {
  .RowActions {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .RowActions div > a {
    display: block;
    border: 1px solid;
    left: 0px;
    padding: 10px;
    margin: 5px;
    text-decoration: none;
  }
}

@media only screen and (max-width: 600px) {
  .list ul {
    margin-block-start: 0px;
    margin-block-end: 0px;
    padding-inline-start: 0px;
  }
  .Row {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .Page h1 {
    font-size: 2em;
    margin-block-end: 0px;
    margin-block-start: 0px;
  }
  .Page h3 {
    margin-block-start: 0px;
  }
  .MarrigeElement {
    margin: 0px;
    padding: 0px;
  }

  .Address {
    margin: 15px auto;
    display: flex;
    flex-direction: column;
    width: 80%;
    align-items: center;
    justify-content: center;
  }
  .Grid1 {
    border: none;
    padding: 10px;
    width: 100%;
    border-bottom: 1px solid;
  }
  .Grid2 {
    border: none;
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid;
    width: 100%;
    padding: 10px;
  }
  .Grid3 {
    display: flex;
    flex-direction: column;
    padding: 10px;
    width: 100%;
  }
  .RowActions {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-top: 10px;
  }
  .RowActions div > a {
    display: block;
    border: 1px solid;
    left: 0px;
    padding: 10px;
    margin: 5px;
    text-decoration: none;
  }
}

.list {
  display: block;
}
.list ul {
  list-style-type: none;
}
.list ul li {
  display: block;
  border-left: 2px solid skyblue;
  padding: 10px;
  margin: 10px;
  text-align: left;
}
.Bg {
  overflow: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 1;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
