@media only screen and (max-width: 600px) {
 .titleOfmodal {
   font-size: 1em;
 }
}

.close {
  outline: 0ch;
  border: none;
  font-weight: bold;
  font-size: 20px;
  padding-right: 24px;
}
.close :hover {
  color: skyblue;
}
#titleModal {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.SplitterTitle {
  border: 1px solid #ccc;
  width: 100%;
}
.modal {
  background-color: white;
  width: 50%;
  border: 1px solid #ccc;
  transition: 1.1s ease-out;
  box-shadow: -2rem 2rem 2rem rgba(0, 0, 0, 0.2);
  filter: blur(0);
  transform: scale(1);
  opacity: 1;
  visibility: visible;
  margin: 0px auto;
  margin-top: 18%;
}
.overlay {
  position: fixed;
  background-color: rgba(255, 255, 255, 0.8);
  width: 100%;
  height: 100%;
}
.modal.off {
  opacity: 0;
  visibility: hidden;
  filter: blur(8px);
  transform: scale(0.33);
  box-shadow: 1rem 0 0 rgba(0, 0, 0, 0.2);
}
@supports (offset-rotate: 0deg) {
  .modal {
    offset-rotate: 0deg;
  }
  .modal.off {
    offset-distance: 100%;
  }
}
@media (prefers-reduced-motion) {
  .modal {
    offset-path: none;
  }
}
.modal h2 {
  padding: 1rem;
  margin: 0;
}
.modal .content {
  padding: 1rem;
}
.modal .actions {
  border-top: 1px solid #ccc;
  background: #eee;
  padding: 0.5rem 1rem;
}
.modal .actions button {
  border: 0;
  background: skyblue;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  font-size: 0.8rem;
  line-height: 1;
}

#centered-toggle-button {
  position: absolute;
}
.close {
  outline: 0ch;
  border: none;
  cursor: pointer;
  background-color: white;
  padding-top: 10px;
}
.white {
  background-color: white;
}
