.Form {
  margin: 10px;
}
.Form div {
  border: 1px solid;
  padding: 10px;
  margin: 10px;
  width: 90%;
}
.Form div > input {
  border: none;
  outline: 0;
  width: 90%;
}
@media only screen and (max-width: 600px) {
  .Form {
    margin: 0px;
  }
  .Form div {
    border: 1px solid;
    padding: 5px;
    margin: 5px;
    width: 90%;
  }
  .Form div > input {
    border: none;
    outline: 0;
    width: 90%;
  }
}
.error {
  border: 1px solid red;
  padding: 10px;
  background-color: white;
}
.success {
  border: 1px solid green;
  padding: 10px;
  background-color: white;
}
.Bg {
  position: fixed;
}
